#mainVisual {
	background-color: #dd7b71;
}

// .front-info ---*/
.front-info {
	background-color: #fff;
	.c-title-sm {
		.en {
			font-size: 32px;
		}
	}
	.item {
		a {
			color: #1a1a1a;
		}
	}

}

// Concept ---*/
.concept {
	background-repeat: no-repeat;
	background-position: center top;
	background-image: url(dir('common/bg_concept.png'));
	@media (max-width: $screen-tab - 1) {
		// background-size: 200%;
		background-image: none;
	}
}

// Freespace ---*/
.freespace-wrap {
}
.freespace {
	color: #dd7b71;
	border-bottom: 1px solid rgba(#fff, .7);
	box-shadow: inset 0 -1px 0 0 rgba(darken(#ffefeb, 10%), .5);
	// box-shadow: 0 3px 5px 0 rgba(#ffefeb - #333, .3);
	img {
		margin-bottom: 10px;
		border: 4px solid #fff;
		box-shadow: 0 5px 15px 0 rgba(darken(#ffefeb, 15%), .7);
	}
	figcaption {
		position: relative;
		color: #dd7b71;
		background-color: transparent;
		// background-color: rgba(#6b5345, .7);
	}
}

// Blog ---*/
.front-blog {
	.thumb {
		color: #dd7b71;
		background-color: lighten(#dd7b71, 25%);
		.not-thumb {
			&:before {
				color: #dd7b71;
			}
		}
	}
	.datetime {
		color: #454545;
	}
}
@media (max-width: $screen-tab - 1) {
	.front-blog {
		background-color: #fff;
		box-shadow: 0 2px 2px 0 rgba(#333, .2);
	}
}
// Catalog ---*/
.front-catalog-wrap {
	.btn-catalog-info {
		color: #fff;
		background-color: #dd7b71;
	}
	.overlay {
		background-color: rgba(#fff, .6);
		&:before {
			content: "";
			position: absolute;
			right: 0;
			bottom: 0;
			display: block;
			width: 0;
			height: 0;
			border: 15px solid transparent;
			border-right-color: $default-color;
			border-bottom-color: $default-color;
		}
	}
	.title {
		color: #222;
	}
	.stylist {
		color: #222;
	}
	.swiper-slide {
		background: #f0f0f0;
	}

	$border-width: 2px;
	@media (min-width: $screen-tab + 1) {
		.swiper-button-prev,
		.swiper-button-next {
			&:before {
				border-top: $border-width solid $swiper-border-color;
			}
		}
		.swiper-button-prev {
			&:before {
				border-left: $border-width solid $swiper-border-color;
			}
		}
		.swiper-button-next {
			&:before {
				border-right: $border-width solid $swiper-border-color;
			}
		}
	}
}


// Front Social ---*/
#instagramJson {
	.nowloading {
		color: #dd7b71;
	}
}
.instagram {
	& > li {
		background-color: #fff;
		box-shadow: 0 2px 3px 1px rgba(#222, .3);
		time {
			color: #555;
		}
	}
}
.social-heading {
	margin-bottom: 5px;
	font-size: 24px;
	color: #e18973;
	// border-bottom: 1px solid #ddd;
	border: none;
}
