$drawer-width: 220px;
// Drawer Navigation ---*/
@media (max-width: $screen-tab) {
  #drawerOverlay {
    background-color: rgba(#222, .7);
  }
}

#drawer {
  background-color: #ffefeb;
  background-repeat: repeat-x;
  background-position: center top;
  background-image: url(dir('common/bg_header_top.png'));
  @media (max-width: $screen-tab - 1) {
    background-size: 60px;
  }
  .drawer-content-back {
    & > header {
      // background-color: #fff;
      // background-image: none;
    }
  }
}


// ドロワーメニュー用のボタン
.drawer {
  &-btn {
    color: #f1c4b1;
    text-shadow: 0 -1px 1px rgba(#1c1c1c, .8);
    background-color: rgba(#1c1c1c, 1);
    border: 1px solid #0b0b0b;
    &:hover {
      background-color: rgba(#1c1c1c, .7);
    }
  }
  &-btn-close {
    background-color: darken($navi-color-mode, 2%);
    border-left-color: $navi-color-mode;
    &:hover {
      background-color: #922;
    }
  }
}
// ドロワーメニュー
.drawer {
  &-navi {
    background-color: #fcfcfc;
    &:before {
      background-color: #fcfcfc;
    }
    .logo-area {
      font-family: 'Playball', cursive;
      color: #fff;
      @include sgradient(
        $colors: (
          (lighten($navi-color-mode, 2%), 0%),
          (darken($navi-color-mode, 2%), 100%)
        )
      );
      border-bottom: 1px solid #999;
    }
  }
  &-inner {
    background-color: #ffefeb;
  }
}
.drawer-gnavi {
  & > li {
    &.current-menu-item {
      color: #f1c4b1;
      background-color: #222;
      > a {
        color: #f1c4b1;
        > .ja {
          color: #f1c4b1;
        }
        > .en {
          color: #fff;
        }
      }
      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 0;
        height: 100%;
        // border: 8px solid transparent;
        // border-top-color: $navi-color-mode;
        // border-left-color: $navi-color-mode;
        border-left: 4px solid $navi-color-mode;
      }
    }
    & > a {
      color: #222;
      border-bottom: 1px solid #f1c4b1;

      > .ja {
        color: #222;
      }
      > .en {
        font-family: $font-google;
        font-size: 24px;
        color: #222;
      }
      &.hover {
        color: #f1c4b1;
        background-color: #222;
        > .ja {
          color: #f1c4b1;
        }
        > .en {
          color: #fff;
        }
      }
    }
  }
} // ul.drawer-gnavi

.drawer-subnavi {
  & > li {
    border-bottom: 1px solid darken(#f1c4b1, 2%);
    & > a {
      color: #fff;
      background-color: #f1c4b1;
      &:hover {
        background-color: #f1c4b1;
      }
    }
  }
}

// グローバルナビゲーション ---*/
.gnavi-block {
  margin-bottom: 0;
  background-color: #1c1c1c;
}
.gnavi {
  & > li {
    background-size: 0 0;
    transition: background-size 500ms ease-in-out;
    a {
      padding: 15px 0 15px;
      color: $navi-color-mode;
    }
    &:last-child {
      & > a {
        &:after {
          // border-right: 1px solid $navi-color-line;
        }
      }
    }
    &.current-menu-item, &:hover {
      // border-bottom: 2px solid $navi-color-mode;
      background-image: url(dir('common/img_navi_hover.png'));
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100%;
    }
    & > a {
      color: $navi-color-jp;

      > .ja,
      > .ja.not-en {
        color: $navi-color-jp;
      }

      > .en {
        margin-bottom: 8px;
        line-height: 1;
        font-family: $font-google;
        font-size: 20px;
        color: $navi-color-en;
      }
      &:hover {
        background-color: transparent;
      }
      &:after {
        height: 34%;
        border-left: 1px solid $navi-color-line;
      }
    }
  }
}
