.en {
	font-family: $font-google;
}
.c-title {
	margin-bottom: 32px;
	padding: 0;
	height: 85px;
	overflow: hidden;
	background-repeat: no-repeat;
	background-position: center -3px;
	// background-size: contain;
	background-image: url(dir('common/tlt_01.png'));
	border-bottom: 1px solid rgba(#fff, .5);
	box-shadow: inset 0 -1px 0 0 rgba(#dd7b71, .2);
	@media (max-width: $screen-tab - 1) {
		margin-left: -15px;
		margin-right: -15px;
	}
	.en {
		// padding-left: 40px;
		// padding-right: 40px;
		margin: 0;
		margin-top: 25px;
		font-weight: 700;
		font-size: 36px;
		color: #1c1c1c;
		&:before, &:after {
			content: "";
			display: none;
		}
	}
	.jp {
		
	}
}
.c-title-sm {
	.en {
		font-size: 28px;
	}
}
.c-title-page {
	margin-top: 0;
	margin-bottom: 32px;
	.en {
		position: relative;
		// padding-left: 24px;
		// padding-right: 24px;
		font-size: 50px;
		color: #1c1c1c;
		&:before, &:after {
			content: "";
			display: none;
		}
	}
	.jp {
		font-size: 14px;
		color: #dd7b71;
	}
	@media (min-width: $screen-tab) {
		.en {
			padding-left: 40px;
			padding-right: 40px;
			font-size: 70px;
			&:before, &:after {
				width: 68px;
				height: 30px;
			}
		}
	}
}
