// Header ---*/
.header-top {
	overflow: hidden;
}
.site-title {
	font-size: 10px;
	color: $header-title-color;
}
.site-logo {
	a {
		&:hover {
			opacity: .8;
		}
	}
}
@media (max-width: $screen-sp) {
	.site-title {
		background-color: $header-title-bg-color;
	}
}
@media (min-width: $screen-tab) {
	.site-title {
		font-size: 11px;
	}
}

.subnavi {
	a {
		font-size: 14px;
		color: $header-subnavi-color;
		background-color: transparent;
		border: none;
		span {
			display: inline;
		}
		&:hover {
			color: darken($header-subnavi-color, 7%);
			text-decoration: underline;
		}
	}
	i {
		font-size: 126%;
		color: $header-subnavi-icon-color;
	}
}

// Header Contact ---*/
.header-contact {
	.btn-default {
		position: relative;
		color: #dd7b71;
		background-color: #fff;
		border: 1px solid #ffd0c4;
		.fa {
			color: #767676;
		}
		& > span {
			&:before, &:after {
				content: "";
				position: absolute;
				bottom: 0;
				display: block;
				width: 10px;
				height: 10px;
				margin-bottom: -1px;
				background-color: #ffefeb;
				border-style: solid;
				border-color: #ffd0c4;
				border-width: 0;
			}
			&:before {
				left: 0;
				margin-left: -1px;
				border-radius: 0 10px 0 0;
				border-right-width: 1px;
				border-top-width: 1px;
			}
			&:after {
				right: 0;
				margin-right: -1px;
				border-radius: 10px 0 0 0;
				border-left-width: 1px;
				border-top-width: 1px;
			}
		}
		&:before, &:after {
			content: "";
			position: absolute;
			top: 0;
			display: block;
			width: 10px;
			height: 10px;
			margin-top: -1px;
			background-color: #ffefeb;
			border-style: solid;
			border-color: #ffd0c4;
			border-width: 0;
		}
		&:before {
			left: 0;
			margin-left: -1px;
			border-radius: 0 0 10px 0;
			border-right-width: 1px;
			border-bottom-width: 1px;
		}
		&:after {
			right: 0;
			margin-right: -1px;
			border-radius: 0 0 0 10px;
			border-left-width: 1px;
			border-bottom-width: 1px;
		}
	}
}

// #spContact ---*/
.contact-sp {
	.contact {
		background-color: $contact-sp-bg-color;
		& > li {
			&:nth-child(2) {
				border-left: 1px solid $contact-sp-border-color;
			}
			& > a {
				color: $contact-sp-color;
			}
		}
	}
}

// Footer ---*/
#footer {
	color: #555;
	background-color: #f1c4b9;
	.footer-link {
		a {
			color: #555;
		}
		.btn-default {
			position: relative;
			color: #dd7b71;
			background-color: #fff;
			.fa {
				color: #767676;
			}
			& > span {
				&:before, &:after {
					content: "";
					position: absolute;
					bottom: 0;
					display: block;
					width: 10px;
					height: 10px;
					margin-bottom: -1px;
					background-color: #f1c4b9;
				}
				&:before {
					left: 0;
					margin-left: -1px;
					border-radius: 0 10px 0 0;
				}
				&:after {
					right: 0;
					margin-right: -1px;
					border-radius: 10px 0 0 0;
				}
			}
			&:before, &:after {
				content: "";
				position: absolute;
				top: 0;
				display: block;
				width: 10px;
				height: 10px;
				margin-top: -1px;
				background-color: #f1c4b9;
			}
			&:before {
				left: 0;
				margin-left: -1px;
				border-radius: 0 0 10px 0;
			}
			&:after {
				right: 0;
				margin-right: -1px;
				border-radius: 0 0 0 10px;
			}
		}
	}
}
.footer-navi-wrap {
	background-color: #fff;
	.foot-navi {
		& > li {
			a {
				color: #c06944;
				&:hover {
					border-bottom: 1px dotted #c06944;
				}
			}
		}
	}

}
// Footer Social Button ---*/
.footer-social-button-wrap {
