.salon-name {
	border-bottom: 1px solid #dd7b71;
	.en {
		color: #1c1c1c;
	}
	.jp {
		color: #888;
	}
}

.salon-info-table {
	th {
		color: #888;
	}
	td {
		color: #1c1c1c;
	}
	th, td {
		border-bottom: 1px solid #dd7b71;
	}
}

.salon-photo-map {
	.item {
		background-color: #fcfcfc;
	}
	.image-map {
		border: 1px solid #ddd;
	}
}

.salon-access-flex {
	border-bottom: 1px solid #dd7b71;
}
.salon-access-title {
	.en {
		font-size: 44px;
		line-height: 1;
	}
}

.salon-access-route {
	color: #222;
	background-color: #fff;
}
