.topics-archive {
	border-bottom: 1px solid #ffd2c7;
}
.topics-header {
	.title {
		a {
			color: #1a1a1a;
		}
	}
}
.topics-content {
	.datetime {
		font-family: $font-google;
		font-size: 20px;
		color: #222;
	}
	@media (max-width: $screen-tab - 1) {
		.content {
			&:after {
				background: transparent;
			}
		}
	}
}
