.stylist-category {
	background-color: #333;
	background-repeat: repeat;
	background-position: center;
	background-image: url(dir('common/bg_free.png'));
	border: 1px solid #000;
	.title {
		color: #fff;
		border-bottom: 1px solid #000;
		&:hover {
			.toggle {
				// color: #fff;
			}
		}
	}
}

// Catalog Search ---*/
.catalog-search-wrapper {
	background-color: #fff;
}
.catalog_search {
	.cr {
		label {
			background-color: #fff;
		}
		input {
			&:checked {
				& + label {
					color: #fff;
					background-color: #dd7b71;
				}
			}
		}

	}
}

// Catalog LoopContent ---*/
.catalog-loop {
	.item {
		background-color: #fff;
		box-shadow: 0 1px 3px 0px rgba(#333, .2);
		transition: all 100ms ease-in;
		&:hover {
			opacity: .8;
		}
		.not-thumb {
			background-color: rgba(#fafafa, .3);
		}
		.border-catalog {
			border-top: 1px solid #ffd2c7;
		}
		.style-name {
			a {
				color: #454545;
			}
		}
		.meta {
			.manage {
				span {
					color: #666;
					background-color: #fff;
					border: 1px solid #ffd2c7;
				}
			}
			.staff {
				// font-size: 16px;
				color: #555;
			}
		}
	}
}


// Single ---*/
.catalog-picture {
	&-body {
		.pic-wrap {
			background-color: #fafafa;
		}
	}
	&-list {
		&-item {
			&.active {
				border-color: $default-color;
			}
		}
	}
	.not-picture {
		color: rgba(#fff, .3);
		background-color: rgba($default-color, .8);
	}
}

// Catalog Content ---*/
.catalog-header {
	.title {
		border-bottom: 1px solid #ddd;
	}
	.comment {
		background-color: #fff;
		&:after {
			border-left: 14px solid #fff;
		}
	}
	.staff-pic {
		.thumb {
			background-color: rgba(#eee, .3);
		}
	}
}

.catalog-table {
	th,td {
		border-bottom: 1px solid #ffd2c7;
	}
	th {
		color: #888;
	}
}
.catalog-tag {
	& > li {
		a {
			color: #888;
			background-color: #f5f5f5;
			border: 1px solid #ffd2c7;
		}
	}
}
.catalog-price {
	.price {
		font-family: $font-google;
		color: #222;
		.txt {
			font-size: 26px;
		}
		.slash {
			font-size: 17px;
		}
		.yen {
			font-size: 19px;
		}
		.numeric {
			font-size: 35px;
		}
		.kara {
			display: inline-block;
			vertical-align: middle;
			line-height: 1;
		}
	}
}

.catalog-footer {
	.other-title {
		letter-spacing: 2px;
		font-family: $font-google;
		font-size: 36px;
		color: #dd7b71;
		// border-bottom: 1px solid #ddd;
	}
}
.other-catalog {
	.swiper-slide {
		a {
			background-color: rgba(#f0f0f0, .3);
		}
		.not-thumb {
			background-color: rgba(#f0f0f0, .3);
		}
	}
}


